@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.is_scrolled .header {
  background-color: rgba(1, 28, 64, 0.6);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  margin: 0 auto;
  padding: 0 103px;
  width: 100%;
  max-width: 1024px;
}
@media (min-width: 1024px) {
  .container {
    max-width: 1572px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  padding: 35px 0;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: inherit;
  z-index: 10;
}
@media (max-width: 1023px) {
  .header {
    padding: 27.5px 0;
    position: absolute;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 20px 0;
  }
}

.header__container {
  display: flex;
}

.header__logo {
  margin-right: auto;
  align-self: baseline;
}

.header__logo svg {
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
}

.header .navigation__list {
  max-width: 650px;
  gap: 18px;
  transform: translateX(8px);
}
@media (max-width: 1023px) {
  .header .navigation__list {
    max-width: 585px;
    gap: 15px;
    transform: translateX(4px);
  }
}
@media (max-width: 767px) {
  .header .navigation__list {
    display: none;
  }
}

.menu-opened .header::before {
  width: 100%;
  height: 100vh;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 43, 102, 0.5);
}

.menu-opened .navigation__button::before, .menu-opened .navigation__button::after {
  position: absolute;
  content: "";
  width: 18.4px;
  height: 2px;
  top: 50%;
  left: 3px;
  background-color: #011c40;
}
.menu-opened .navigation__button::before {
  transform: rotate(45deg);
}
.menu-opened .navigation__button::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .menu-opened .header__logo {
    z-index: -10;
  }
}

@media (max-width: 767px) {
  .menu-opened .navigation__logo {
    display: flex;
    position: relative;
    right: 210px;
  }
}

@media (max-width: 767px) {
  .menu-opened .navigation__list--header {
    padding: 75px 30px 75px 15px;
    width: 285px;
    height: 100vh;
    display: block;
    position: fixed;
    right: 0;
    top: 0;
    overflow: auto;
    background-color: #f9fbfd;
  }
}

@media (max-width: 767px) {
  .menu-opened .navigation__item:not(:last-child) {
    margin-bottom: 10px;
  }
}

@media (max-width: 767px) {
  .menu-opened .navigation__item a {
    padding: 10px 10px 8px;
    width: 100%;
    display: block;
    text-shadow: none;
  }
}

.logo {
  display: flex;
  align-items: center;
}

.logo svg {
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .logo svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo svg {
    width: 42px;
    height: 22px;
  }
}

.navigation__logo {
  display: none;
  z-index: 100;
}

.navigation__logo svg {
  fill: #011c40;
}

.navigation__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
}

@media (max-width: 767px) {
  .navigation__list--header {
    display: none;
  }
}

.navigation__item {
  display: flex;
}
@media (hover: hover) {
  .navigation__item:hover:not(.focus-visible) a {
    text-shadow: 0 4px 4px rgba(255, 255, 255, 0.75);
  }
}
.navigation__item.focus-visible:focus a {
  text-shadow: 0 4px 4px rgba(255, 255, 255, 0.75);
}

.navigation__item a {
  padding: 8px;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .navigation__item a {
    padding: 4px;
  }
}

.navigation__item--header a {
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .navigation__item--header a {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .navigation__item--header a {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}

@media (max-width: 1023px) {
  .navigation__item--footer a {
    font-size: 12px;
    line-height: 15px;
  }
}

.navigation__button {
  padding: 0;
  width: 24px;
  height: 24px;
  display: none;
  position: absolute;
  top: 18px;
  right: 15px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 100;
}
.navigation__button::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 2px;
  top: 50%;
  left: 0;
  background-color: #f9fbfd;
  box-shadow: 0 -6px 0 0 #f9fbfd, 0 6px 0 0 #f9fbfd;
}
@media (max-width: 767px) {
  .navigation__button {
    display: block;
  }
}

.socials {
  padding: 0;
  margin: 0;
  width: 106px;
  display: flex;
  justify-content: space-between;
  list-style: none;
}
@media (max-width: 1023px) {
  .socials {
    width: 87px;
  }
}

.socials__item a {
  display: flex;
  align-items: center;
}
@media (hover: hover) {
  .socials__item a:hover:not(.focus-visible) svg {
    filter: drop-shadow(0 4px 4px rgba(255, 255, 255, 0.75));
  }
}
.socials__item a.focus-visible:focus svg {
  filter: drop-shadow(0 4px 4px rgba(255, 255, 255, 0.75));
}

.footer {
  padding: 50px 0;
  background-color: #011c40;
}
@media (max-width: 1023px) {
  .footer {
    padding: 50px 0 47px;
  }
}
@media (max-width: 767px) {
  .footer {
    padding: 40px 0 37px;
  }
}

.footer__container {
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .footer__container {
    flex-direction: column;
    align-items: flex-start;
  }
}

.footer .logo {
  margin-right: 30px;
}
@media (max-width: 1023px) {
  .footer .logo {
    margin-right: 21px;
  }
}
@media (max-width: 767px) {
  .footer .logo {
    margin-right: 0;
    margin-bottom: 25px;
  }
}

.footer .navigation__list {
  max-width: 620px;
  gap: 18px;
}
@media (max-width: 1023px) {
  .footer .navigation__list {
    max-width: 450px;
    gap: 15px;
  }
}
@media (max-width: 767px) {
  .footer .navigation__list {
    margin-bottom: 20px;
    max-width: auto;
    flex-direction: column;
    gap: 9px;
  }
}

.footer .socials {
  margin-left: auto;
}
@media (max-width: 767px) {
  .footer .socials {
    margin-left: 0;
  }
}

.intro {
  padding: 108px 0;
  min-height: 650px;
  background-color: #011c40;
  background-image: url("../img/intro.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: -10;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .intro {
    background-image: url("../img/intro@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .intro {
    padding: 80px 0;
    min-height: 638px;
    filter: none;
  }
}
@media (max-width: 767px) {
  .intro {
    padding: 62px 0;
    min-height: 500px;
  }
}

.intro__content {
  margin-top: 162px;
  margin-left: 95px;
  color: #f9fbfd;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .intro__content {
    margin-top: 188px;
    margin-left: 0;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .intro__content {
    margin-top: 114px;
  }
}

.intro__content h1 {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
}
@media (max-width: 1023px) {
  .intro__content h1 {
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .intro__content h1 {
    margin-bottom: 10px;
    font-size: 36px;
    line-height: 45px;
  }
}

.intro__content p {
  margin: 0;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .intro__content p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .intro__content p {
    font-size: 16px;
    line-height: 22.4px;
  }
}

.about {
  padding: 120px 0 145px;
}
@media (max-width: 1023px) {
  .about {
    padding: 90px 0 94px;
  }
}
@media (max-width: 767px) {
  .about {
    padding: 57px 0 85px;
  }
}

.about__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 40px;
}
@media (max-width: 1023px) {
  .about__container {
    padding: 0 50px 0 45px;
