    column-gap: 32px;
  }
}
@media (max-width: 767px) {
  .about__container {
    display: block;
    column-gap: 0;
  }
}

.about__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 767px) {
  .about__content {
    margin-bottom: 44px;
  }
  .about__content br {
    display: none;
  }
}

.about__content h2 {
  margin: 0;
  margin-bottom: 22px;
  padding: 0;
  font-size: 28px;
  line-height: 42px;
  font-weight: 600;
}
@media (max-width: 1023px) {
  .about__content h2 {
    margin-bottom: 15px;
    font-size: 26px;
    line-height: 36.4px;
  }
}
@media (max-width: 767px) {
  .about__content h2 {
    font-size: 24px;
    line-height: 33.6px;
  }
}

.about__content p {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 24px;
}

.about__photo {
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .about__photo {
    margin-top: 10px;
  }
}

.advantages {
