  .advantages {
    background-image: linear-gradient(to top, rgba(227, 236, 246, 0.9), rgba(227, 236, 246, 0.9)), url("../img/advantages@2x.jpg");
  }
}

.advantages__container {
  padding-top: 110px;
  padding-bottom: 120px;
}
@media (max-width: 1023px) {
  .advantages__container {
    padding-top: 90px;
    padding-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .advantages__container {
    padding-top: 57px;
    padding-bottom: 65px;
  }
}

.advantages h2 {
  margin: 0;
  margin-bottom: 49px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
}
@media (max-width: 1023px) {
  .advantages h2 {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .advantages h2 {
    margin-bottom: 30px;
    font-size: 24px;
    line-height: 36px;
  }
