.products__item-wrapper {
  min-height: 386px;
  position: relative;
}
@media (hover: hover) {
  .products__item-wrapper:hover:not(.focus-visible) .products__item-front {
    transform: perspective(1000px) rotateY(-180deg);
  }
  .products__item-wrapper:hover:not(.focus-visible) .products__item-back {
    transform: perspective(1000px) rotateY(0deg);
  }
}
.products__item-wrapper.focus-visible:focus .products__item-front {
  transform: perspective(1000px) rotateY(-180deg);
}
.products__item-wrapper.focus-visible:focus .products__item-back {
  transform: perspective(1000px) rotateY(0deg);
}

.products__item-front,
.products__item-back {
  width: 100%;
  height: 100%;
  position: absolute;
  backface-visibility: hidden;
  overflow: hidden;
  transition: transform 0.6s linear;
}

.products__item-front {
  height: 100%;
  transform: perspective(1000px) rotateY(0deg);
}
.products__item-front::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(180deg, transparent 50%, #011c40 100%);
}

.products__item-front img {
  height: 100%;
  object-fit: cover;
}

.products__item-front h3 {
  margin: 0;
  padding: 23px 30px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .products__item-front h3 {
    padding: 26px 35px;
  }
}
@media (max-width: 767px) {
  .products__item-front h3 {
    padding: 18px 25px;
  }
}

.products__item-back {
  padding: 29px 25px;
  width: 100%;
  color: #f9fbfd;
  background-color: #011c40;
  transform: perspective(1000px) rotateY(180deg);
}
@media (max-width: 1023px) {
  .products__item-back {
    padding: 30px 35px;
  }
}
@media (max-width: 767px) {
  .products__item-back {
    padding: 28px 25px;
  }
}

.products__item-back p {
  margin: 0;
  margin-bottom: 12px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}

.products__item-back dl {
  margin: 0;
  margin-bottom: 21px;
  height: 68%;
  overflow: auto;
}
.products__item-back dl::-webkit-scrollbar {
  width: 2px;
}
.products__item-back dl::-webkit-scrollbar-thumb {
  background-color: #f9fbfd;
}

.products__item-back dt {
  font-weight: 500;
  font-size: 14px;
  line-height: 19.6px;
}

.products__item-back dd {
  margin: 0;
