  color: #d8d8d9;
}
.products__item-back dd:not(:last-child) {
  margin-bottom: 14px;
}

.products__item-back a {
  padding: 9px 10px;
  display: block;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
}
@media (hover: hover) {
  .products__item-back a:hover:not(.focus-visible) {
    box-shadow: inset 0 4px 4px rgba(0, 0, 0, 0.25);
  }
}
.products__item-back a.focus-visible:focus {
  box-shadow: inset 0 4px 4px rgba(0, 0, 0, 0.25);
}

